@import '../../styles/colors';

.history {
  width: 90vw;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  margin-top: 1rem;
}

.list {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.item {
  list-style: none;
  font-size: 1rem;
  margin-right: 1rem;
  color: $secondary-color;
  cursor: pointer;

  &:last-child {
    margin-right: 0;
  }
  &:hover {
    color: $secondary-light;
  }
}

.buttons {
  margin-top: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.expand {
  margin-right: 1rem;
}

.button {
  padding: .5rem .75rem;
}

@media only screen and (max-width: 520px) {
  .list {
    flex-direction: column;
  }
  .item {
    margin-bottom: 1rem;
    margin-right: 0;
    &:last-child {
      margin-bottom: 0;
    }
  }
}